.Header {
  padding: 0.4rem 2rem;
  border-bottom: 1px solid rgba(189, 189, 192, 0.2);
  font-size: 0.6rem;
}

.Header__name {
  color: #fff;
}

.Header__picture {
  width: 80px;
  border-radius: 50%;
  float: right;
  padding: 20px 10px;
}
@media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}
