@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif;
}

#root {
  position: fixed;
  height: 90vh;
}

/* scroll bar */
body::-webkit-scrollbar {
  width: 10px;
}
body::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 20px;
}
body::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 20px;
}

@media (min-width: 576px) {
  #root {
    position: static;
    /* default value is static */
  }
}

.Sidebar {
  width: 80px;
  background-color: rgba(32, 32, 35, 1);
  height: 100%;
  border-right: 1px solid rgba(189, 189, 192, 0.1);
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  overflow-y: scroll;
}

/* not small devices */
@media (min-width: 576px) {
  .Sidebar {
    width: 320px;
  }
}

/* Scroll Bar */
::-webkit-scrollbar {
  width: 0px; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
/* Scroll bar for not small devices */
@media (min-width: 576px) {
  .Sidebar::-webkit-scrollbar {
    width: 0.5em;
    min-width: 8px;
    overflow-y: scroll;
  }
  .Sidebar::-webkit-scrollbar-track-piece {
    background: #222222;
    width: 2px;
    box-shadow: none;
    -webkit-box-shadow: none;
  }
  .Sidebar::-webkit-scrollbar-thumb {
    background-color: #ff0000;
  }

  /* Firefox hide scrollbar */
  .Sidebar {
    scrollbar-width: none;
  }
}

.User {
  display: flex;
  align-items: flex-start;
  padding: 1rem;
}
.User:hover {
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.User__pic {
  width: 50px;
  border-radius: 50%;
}
.User__details {
  display: none;
}

/* not small devices  */
@media (min-width: 576px) {
  .User__details {
    display: block;
    padding: 0 0 0 1rem;
  }
  .User__details-name {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-size: 1rem;
  }
}

.Main {
  flex: 1 1;
  background-color: rgba(25, 25, 27, 1);
  height: 100%;
}

.Empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.Empty__name {
  color: #fff;
}
.Empty__status,
.Empty__info {
  padding: 1rem;
}
.Empty__status {
  color: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}
.Empty__img {
  border-radius: 50%;
}
.Empty__btn {
  padding: 1rem;
  margin: 1rem 0;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 30px;
  outline: 0;
}
.Empty__btn:hover {
  background: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}

@media (min-width: 567px) {
  .Empty__img {
    margin: 2rem 0;
  }
}

.Header {
  padding: 0.4rem 2rem;
  border-bottom: 1px solid rgba(189, 189, 192, 0.2);
  font-size: 0.6rem;
}

.Header__name {
  color: #fff;
}

.Header__picture {
  width: 80px;
  border-radius: 50%;
  float: right;
  padding: 20px 10px;
}
@media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}

.Chats {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 85%;
  margin: 0 auto;
  overflow-y: scroll;
  /* Hide scrollbar in Firefox */

  scrollbar-width: none;
}
.Chat {
  margin: 1rem 0;
  color: #fff;
  padding: 1rem;
  background: -webkit-gradient(linear, left top, right top, from(#1986d8), to(#7b9cc2));
  background: -webkit-linear-gradient(left, #1986d8, #7b9cc2);
  background: linear-gradient(90deg, #1986d8, #7b9cc2);
  max-width: 90%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.Chat.is-user-msg {
  margin-left: auto;
  background: #2b2c33;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

@media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}

.Message {
  width: 80%;
  margin: 1rem auto;
}
.Message__input {
  width: 100%;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  outline: 0;
}

.ChatWindow {
  display: flex;
  flex-direction: column;
  height: 90vh;
}

.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);
}

